import uniPopup from "@/components/uni-popup/uni-popup.vue";
export default {
  components: {
    uniPopup
  },
  data() {
    return {
      // 控制发送以及剩余时间的切换显示
      isLogin: true,
      // 定时器命名
      timer: "",
      // 倒计时内容
      num: 60,
      // 输入的手机号码
      phone: null,
      // 输入的验证码
      code: null
    };
  },
  methods: {
    // 点击获取验证码
    getCode() {
      // 调用定时器
      this.countDown();
      // 手机号码的正则表达式
      var reg =
        /^(((13[0-9]{1})|(15[0-9]{1})|(16[0-9]{1})|(17[3-8]{1})|(18[0-9]{1})|(19[0-9]{1})|(14[5-7]{1}))+\d{8})$/;
      if (reg.test(this.phone)) {
        this.isLogin = false;
        this.$http
          .post("/API/VshopProcess.ashx", {
            action: "GetCode",
            phone: this.phone
          })
          .then(res => {
            console.log(res);
          });
      }
    },
    // 打开遮罩层
    open() {
      this.$refs.popup.open();
    },
    getPhoneNumber(e) {
      console.log(e);
      // uni.login({
      // 	provider: 'weixin',
      // 	success: (res) => {
      // 		console.log(res);
      // 	}
      // });
      // uni.switchTab({
      // 	url: '/pages/me/me'
      // });
    },
    // 倒计时效果
    countDown() {
      // 获取倒计时的初始值
      var time = this.num;
      // 为定时器命名
      this.timer = setInterval(() => {
        // 每隔一秒 num 就减一 实现同步
        time--;
        // 然后把 num 存进 data, 让用户知道时间在倒记着
        this.num = time;
        if (time == 0) {
          clearInterval(this.timer);
          // 当时间为 0 的时候 隐藏定时器的内容 显示发送的内容 并且为定时器重新赋值
          this.isLogin = true;
          this.num = 60;
        }
      }, 1000);
    }
  }
};
